.pagination {
    display: flex;
    justify-content: center;
    align-items: center;
}

.pagination span {
    width: 2rem;
    height: 2rem;
    padding: 5px;
    margin: 5px;
    border-radius: 50px;
    display: flex;
    align-items: center;
    justify-content: center;
    border: 1px solid var(--primary-color);
    color: var(--heading-color);
    font-size: 1.1rem;
    cursor: pointer;
    font-weight: 700;
}

.active_page {
    background: var(--primary-color);
    color: #fff !important;
}